	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"PyPy.js is an experiment in building a fast, compliant, in-browser python interpreter.  By compiling the PyPy interpreter into javascript, and retargeting its JIT compiler to emit asmjs code at runtime, it is possible to run python code in the browser at speeds competitive with a native python environment.  This talk will demonstrate the combination of technologies that make such a thing possible, the results that have been achieved so far, and the challenges that still remain when trying to take python onto javascript's home turf.\n\nWe'll cover: an overview of PyPy and why it's a good fit for this type of project; an introduction to asmjs and the rise of javascript as a compile target; what it looks like when you smoosh these two technologies together; a comparison with other approaches such as brython; and some concrete suggestions for how the result might be useful in practice.\n\nPyCon Australia is the national conference for users of the Python Programming Language. In August 2014, we're heading to Brisbane to bring together students, enthusiasts, and professionals with a love of Python from around Australia, and all around the World. \n\nAugust 1-5, Brisbane, Queensland, Australia","meta":{"duration":"PT25M38S"},"presenter":{"name":null},"source":{"id":"8C9q94F6Uqo","provider":"youtube"},"slug":"pypyjs-what-how-why-by-ryan-kelly","tag":["pyconau","pycon-au 2014","Python","PyCon"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/8C9q94F6Uqo/default.jpg","height":90,"width":120}},"title":"PyPy.js: What? How? Why? by Ryan Kelly"}}]}},"pathContext":{"slug":"pycon-au-2014","tag":"pycon-au 2014"}}

